@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

.cecutient-version {
	// base
	$font-size: 1.6rem;
	$line-height: round-float(3.0rem/$font-size, 2);
	// ===============
	$border-color: black;
	$border-width: 2px;
	// ===============
	$animation-none: true;
	$animation-speed: 0.0s;
	// =======mixin========

	//=========== TYPOGRAPHY ==========
	& {
		*::selection {
			background-color: black;
			color: white; } }

	hr {
		border-width: $border-width; }

	.breadcrumbs__list a, .#{$user-content} a:not(.btn-default):not(.btn-default-alt):not(.view-print):not(.icon-jfi-trash):not(.promo-slide):not(.news-previews__link):not(.news-previews__label):not(.several-slider__inner):not(.popular__inner):not(.structure-panel-item):not(.ph-gallery__inner):not(.ph-gallery-all) {
			padding-top: 0.2em;
			text-decoration: none;
			border-bottom: $border-width solid $border-color;
			box-shadow: -#{$border-width*2} 0 0 transparent, #{$border-width*2} 0 0 transparent;

			@if $animation-none {
				transition: none; }
			@else {
				transition: {
					property: border-color, background-color, color, box-shadow;
					duration: $animation-speed; } } }

	// table
	.#{$user-content} {

		.tbl {

			th, thead td {
				border-bottom-width: $border-width; } } }

	// blockquote
	.#{$user-content} {
		blockquote {
			padding: 10px 12px;
			border: #{$border-width} solid $border-color;

			q, cite {
				@include font-medium;

				&::before {
					content: none; } } } }

	// add shadow
	.add-shadow, .add-shadow--full {

		&::after {
			content: none; } }

	.add-shadow--full {
		border-width: $border-width; }

	// add shadow end

	// entry
	.entry-img {
		padding: 0;
		height: auto;

		img {
			display: none; } }

	.entry-img__title {
		position: relative;
		@include position(0);
		transform: none;
		text-shadow: none; }

	// entry end

	// files links
	.file {
		box-shadow: none;
		padding: 0.5em !important; // перебить стили ссылок
		transition-duration: 0s !important; // перебить стили ссылок
		border: none !important; // перебить стили ссылок

		& > span, & > em, path {
			@if $animation-none {
				transition: none; }
			@else {
				transition-duration: $animation-speed; } }

		.no-touchevents &:hover {
			box-shadow: none; } }

	// files links end

	// view print
	.view-print {
		box-shadow: none !important;
		border: #{$border-width} solid #{$text-color-base};
		@include font-bold;
		font-size: 1.8rem;
		letter-spacing: 0.05em;
		padding: 8px 10px;

		&, path {
			@if $animation-none {
				transition: none; } } }

	// view print end

	// logo
	// logo in header
	.logo {
		a {
			background-image: url(../../img/logo-full-bw.svg); } }
	// logo in header end

	// logo in sidebar
	.logo-side {
		@if $animation-none {
			will-change: none;
			transition: none; }

		.page-is-scrolled & {
			@if $animation-none {
				transition: none; } } }

	.logo-side-inner {
		&::before, &::after {
			@if $animation-none {
				will-change: none;
				transition: none; } }

		&::before {
			background-image: url(../../img/logo-i-bw.svg); }

		&::after {
			background-image: url(../../img/logo-t-bw.svg); } }

	// logo in sidebar end
	// logo end

	// short contacts
	.short-contacts {
		a {
			border-bottom: none !important;
			box-shadow: none !important;
			background: none !important;
			padding-top: 0 !important;

			&[href] {
				span {
					@if $animation-none {
						transition: none; } } } } }

	// short contacts end
	//=========== TYPOGRAPHY end ==========

	//=========== BUTTONS ==========
	// button

	// btn-default
	[class*="btn-default"] {
		color: $bg-color-base;
		fill: $bg-color-base;
		background: $text-color-base;

		&:not(.btn-default-alt):not(.disabled):not([data-disabled]):not([disabled]) {
			box-shadow: inset 0 0 0 2px $text-color-base; }

		&::before, &::after {
			display: none; }

		@if $animation-none {
			transition: none; }

		path {
			@if $animation-none {
				transition: none; } } }

	// btn-default end

	// btn-default-alt
	.btn-default-alt {
		color: $text-color-base;
		border-color: $text-color-base;
		background: $base-color-alt;

		.no-touchevents &:hover {
			border-color: $base-color;
			color: $base-color; } }

	// btn-default-alt end

	// btn-with-arrow
	.btn-with-arrow {

		& > i {
			&::before, &::after {

				@if $animation-none {
					transition: none; }

				path {
					@if $animation-none {
						transition: none; } } } } }

	// btn-with-arrow end

	// button toggle menu
	.menu-btn {
		&, &::before, &::after {
			@if $animation-none {
				transition: none; } }

		& > i {
			&, &::before, &::after {
				@if $animation-none {
					transition: none; } } } }

	// button toggle menu end
	// button end
	//=========== BUTTONS END ==========

	//=========== FORM STYLES DEFAULT ==========
	input[type="text"], input[type="password"], input[type="email"], input[type="search"], textarea,
	.select select,
	.select2-container--default .select2-selection .select2-selection__rendered {
		font-size: 18px; }

	.error-note {
		@include padding-horizontal(5px);
		height: 24px;
		font-size: 16px;
		line-height: 24px; }

	.confirm-note {
		$this-icon-lines-width: 12px;
		$this-icon-lines-height: 3px;

		&::before {
			@include size(round($this-icon-lines-width*0.75), $this-icon-lines-height); }

		&::after {
			@include size(round($this-icon-lines-width*1.3), $this-icon-lines-height); } }

	// select
	.select2-container--default {
		.select2-results__option[aria-selected=true] {
			&::before {
				@include pseudo-inline-block();
				vertical-align: top;
				margin: 0.6em 0.5em 0 0;
				@include size(10px, 3px);
				background-color: $text-color-base; }

			&.select2-results__option--highlighted {
				&::before {
					background-color: $bg-color-base; } } } }

	// select end

	//=========== FORM STYLES DEFAULT END ==========

	// search form
	.search-form, .btn-search, .btn-search-close {
		&::after {
			display: none; }

		@if $animation-none {
			transition: none; } }

	.search-form {
		input {
			@include font-bold;
			font-size: 22px;

			@include placeholder {
				@if $animation-none {
					transition: none; } } } }

	.btn-search, .btn-search-close {
		i {
			&::before, &::after {
				@if $animation-none {}
				transition: none; } }

		path {
			@if $animation-none {
				transition: none; } } }

	// search form end

	// jFiler
	.jFiler-input-dragDrop {
		@if $animation-none {
			transition: none; } }

	.jFiler-item-trash-action {
		border-bottom: none !important;
		box-shadow: none !important;
		padding-top: 0 !important;

		&::before, &::after {
			@if $animation-none {
				transition: none; } } }

	// jFiler end

	// social networks
	.social-networks__item {
		a {
			&, & path {
				@if $animation-none {
					transition: none; } } } }

	// social networks end

	// filters
	.filters {
		a {
			padding-top: 0 !important;

			&, & span {
				@if $animation-none {
					transition: none; } }

			span {
				border-bottom: none; } } }

	// filters end

	// pagination
	.pg-first, .pg-last, .pg-prev, .pg-next, .pg-list a {
		background: none !important;
		box-shadow: none !important;
		border-bottom: none !important;
		padding-top: 0 !important; }

	.pg-first-arr, .pg-last-arr, .pg-prev-arr, .pg-next-arr {
		&, &::before, &::after {
			@if $animation-none {
				transition: none; } } }

	.pg-list {
		li {
			a, & > span {
				@if $animation-none {
					transition: none; } } } }

	// pagination end

	//=========== LAYOUT ==========
	&.home-page {
		&::before {
			content: none; } }

	.bg-img {
		display: none; }

	.wrapper {
		&::before, &::after {
			content: none; } }

	// content head
	.content-head {
		border-bottom: 1px solid $border-color; }

	// content head end

	//=========== LAYOUT END ==========

	// user options
	.user-options {
		@if $animation-none {
			transition: none; } }

	.user-options__item {
		& > a, svg, path {
			@if $animation-none {
				transition: none; } } }

	.theme-toggle {
		display: none; }

	// user options end

	// lang
	.lang {
		& > a {
			& > span {
				&::before, &::after {
					@if $animation-none {
						transition: none; } } } } }

	.lang__list {
		box-shadow: none;
		border-width: 1px;
		@if $animation-none {
			transition: none; }

		a {
			border-width: 1px 0 0;

			@if $animation-none {
				transition: none; }

			& > * {
				font-size: 18px; } } }

	// lang end

	.sidebar {
		.user-options {
			box-shadow: none !important; } }

	// divider
	.divider {
		border-top-width: $border-width; }

	// divider end

	//=========== SLIDERS ==========
	// arrows
	.slick-arrow {
		@if $animation-none {
			transition: none; }

		&::before, &::after {
			border-color: $text-color-base; }

		.no-touchevents &:not(.slick-disabled):hover {
			background-color: transparent;
			box-shadow: 0 0 0 2px $text-color;

			&::before, &::after {
				border-color: $text-color-base; } } }

	// promo-slide
	.promo {
		.slider-arrows {
			display: none; } }

	.promo-slider {
		opacity: 1;
		visibility: visible;
		padding-bottom: 0; }

	.promo-slide {
		display: block;
		white-space: normal;
		opacity: 1;
		visibility: visible;
		width: 100%;
		height: auto;
		min-height: 0;
		border-top: #{$border-width} solid #{$border-color};
		padding: 10px 15px;

		&:first-child {
			border-top: none; } }

	.promo-slider__figure {
		display: none; }

	.promo-slider__text {
		position: relative;
		@include position(0);
		width: auto;
		max-width: none;
		color: $text-color;
		font-size: 22px;
		padding: 0;

		@include media('<desk-md') {
			font-size: 18px; }

		span {
			background: none;
			box-shadow: none; } }

	// promo-slide end

	// several slider
	.several-slider {
		.slider-arrows {
			display: none; } }

	.several-slider-list {
		white-space: normal;
		overflow: visible;
		opacity: 1;
		visibility: visible;
		border: none;

		@if $animation-none {
			transition: none; } }

	.several-slider__item {
		display: block;
		background: none;
		margin-bottom: $bottom-space--sm;

		@include media('<mob-md') {
			border-color: transparent; }

		&::before {
			display: none; } }

	.several-slider__item, .several-slider__inner, .several-slider__text, .several-slider__value {
		@if $animation-none {
			transition: none; } }

	.several-slider__inner {
		@include display-inline-block-top;
		text-align: left;
		padding: 0;
		background: none;

		&::after {
			display: none; }

		@include media('<tab-lg') {
			display: inline; } }

	.several-slider__figure,
	.duplicate .info-links__icon {
		display: none; }

	.several-slider__text {
		margin-right: 10px; }

	.several-slider__text,
	.several-slider__content,
	.several-slider__value,
	.several-slider__unit {
		display: inline-block;
		padding: 0; }

	.duplicate .info-links__text {
		display: inline;
		padding: 0;
		border-bottom: 2px solid #{$border-color}; }

	// several slider end
	//=========== SLIDERS end ==========

	// submenu
	.submenu-list {

		&::after, &::before {
			display: none !important;
			content: none !important; } }

	// submenu end

	// navigation
	.nav-list {
		a {

			@if $animation-none {
				transition: none; } } }

	.nav__drop {
		border-color: $border-color;
		background-color: $bg-color-base; }

	// navigation end

	// caption box / caption
	.caption-box, .caption {
		display: block; }

	.caption::before {
		content: none; }

	.caption-box__title, .caption__title {
		display: block;
		color: $title-color;
		width: auto;
		padding-right: 0;
		margin-bottom: $bottom-space;

		br {
			display: none; } }

	.caption-box__content, .caption__text {
		display: block;
		width: auto;
		padding-left: 0; }

	// caption box / caption end

	// benefits counter
	.benefits-counter {
		@include margin-horizontal(0); }

	.benefits-counter__item {
		display: block;
		width: auto;
		@include padding-horizontal(0);
		padding-bottom: $bottom-space--sm;

		&:last-child {
			padding-bottom: 0; }

		.dt, .dd {
			@include display-inline-block-top;
			line-height: 52px;
			margin: 0;
			min-width: 0; }

		.dt {
			padding-right: 15px; } }

	// benefits counter end

	// news previews
	.news-previews__list, .list-view .news-previews__list {
		@include grid-parent($bottom-space--md); }

	.news-previews__item, .list-view .news-previews__item {
		display: block;
		@include grid-item-responsive(1, $bottom-space--md);
		width: auto;

		//&.wow
		//, &:nth-child(3n-1), &:nth-child(3n), &:nth-child(2n)
		//transition:
 }		//	delay: 0s

	.news-previews__meta, .news-previews__link, .list-view .news-previews__meta, .list-view .news-previews__link {
		margin-bottom: $bottom-space--sm;
		margin-top: 0;
		padding-left: 0; }

	.news-previews__figure {
		display: none; }

	.news-previews__label, .news-previews__title, .news-previews__text {
		@if $animation-none {
			transition: none; } }

	.list-view {
		.news-previews__meta, .news-previews__content, .news-previews__footer {
			float: none;
			width: auto;
			padding-left: 0;
			margin-top: 0; }

		.news-previews__content {
			margin-bottom: $bottom-space--sm; } }

	// news previews end

	// fame
	.fame__figure {
		&::before, &::after {
			content: none; } }

	.fame__img {
		position: relative;
		top: auto;
		left: auto;
		width: auto;
		height: auto;
		transition: none !important;
		transform: none !important;

		&::before {
			@include pseudo-block-relative();
			@include ratio(286, 410); }

		&::after {
			content: none; } }

	.fame__info {
		position: relative;
		transition: none !important;
		transform: none !important;
		border: none;
		box-shadow: none;
		@include padding-horizontal(0);

		a {
			border-bottom: none !important;
			padding-top: 0 !important;
			box-shadow: none !important;
			background: none !important; } }

	// fame end

	// subscription form
	.subscription-form {

		input[type='text'], input[type='email'] {
			border-width: $border-width;
			box-shadow: none;

			@include placeholder {
				color: $text-color-base; } }

		input[type='submit'] {
			@if $animation-none {
				transition: none; } } }

	.input-holder, .success-text, .error-text {
		font-size: 14px;
		@if $animation-none {
			transition: none; } }

	.success-text {
		span {
			&::before, &::after {
				@if $animation-none {
					transition: none; } } } }

	// subscription form end

	// popular
	.popular__inner {
		@if $animation-none {
			transition: none; } }


	// popular end

	// visual preview
	.visual-preview {
		display: none; }

	// visual preview end

	// structure
	.structure-hand {
		@if $animation-none {
			transition: none;

			.angle {
				&::before, &::after {
					@if $animation-none {
						transition: none; } } } } }

	.structure-panel-item {
		@if $animation-none {
			transition: none; } }

	// structure end

	// info links
	.info-links__inner {
		&, &::before {
			@if $animation-none {
				transition: none; } }

		&::after {
			display: none; }

		@include media('<tab-lg'); }

	// info links end

	// photo gallery
	.ph-gallery__img {

		img, .ph-gallery__zoom {
			transform: none !important;

			@if $animation-none {
				transition: none; } }

		&::after {
			display: none !important; } }

	.ph-gallery-all {
		background-color: $text-color-base;

		&::after {
			content: none; } }

	.ph-gallery__zoom {
		background-color: $text-color-base; }

	// photo gallery end

	//=========== HEADER ==========

	.header__holder {
		@include media('<mob-lg') {
			box-shadow: none !important; } }

	//=========== HEADER END ==========

	//=========== SIDEBAR ==========
	// menu
	.menu-list, .sidebar .nav-list {

		a {
			border: #{$border-width} solid transparent; } }

	// menu end

	// nav in sidebar
	.sidebar .nav::before {
		border-color: $border-color; }

	// nav in sidebar end

	//=========== SIDEBAR END ==========

	//=========== ASIDE ==========
	// actual links
	.actual-links {
		a {
			@include display-inline-block-top;
			padding: 3px;

			&, & span {
				@if $animation-none {
					transition: none; } } } }

	// actual links end
	//=========== ASIDE END ==========

	//=========== FOOTER ==========
	// footer gov
	.gov__list {
		border: none;
		padding: 0; }

	.gov__item, .gov__inner {
		@if $animation-none {
			transition: none; } }

	.gov__item {
		display: block;
		padding: 0;
		margin-top: $bottom-space--sm;

		&:first-child {
			margin-top: 0; } }

	.gov__inner {
		display: inline;
		border: none;
		padding: 0;

		&::before {
			display: inline;
			content: attr(title);
			white-space: normal;
			border-bottom: #{$border-width} solid #{$border-color}; } }

	.gov__figure {
		display: none; }

	// footer gov end

	// footer common
	.footer-common {
		a {
			@if $animation-none {
				transition: none; } } }

	// footer common end

	// developer
	.developer__logo {
		&::before, &::after {
			@if $animation-none {
				transition: none; } } }

	// developer end
 }	//=========== FOOTER END ==========
